:root {
  --nitro-blue: #5bacc3;
  --nitro-pink: #fff0ed;
  --graphite: #393939;
  --light-grey: #f7f7f7;
  --grey: #979797;
  --card-background: #fff;
}

footer, .footer {
  display: flex;
  background-color: var(--nitro-pink);
  justify-content: space-between;
}
footer__socials ul, .footer__socials ul {
  padding: 0;
  list-style: none;
}
footer__socials ul li, .footer__socials ul li {
  display: inline-block;
}
footer__socials img, .footer__socials img {
  width: 50px;
}

.primary-nav {
  display: flex;
  background-color: var(--nitro-pink);
  align-items: center;
  justify-content: space-between;
  padding: 2rem 2rem;
}
.primary-nav__logo {
  display: flex;
  align-items: center;
  margin: 0;
}
.primary-nav__logo a {
  display: flex;
  align-items: center;
  justify-content: center;
}
.primary-nav__logo img {
  width: 50px;
}
.primary-nav__logo h1 {
  margin: 0;
}
.primary-nav__list {
  display: flex;
  list-style: none;
  font-size: 1.313rem;
}
.primary-nav__list li {
  margin-right: 24px;
}
.primary-nav h1 {
  text-decoration: none;
  /* font-size: 2rem; */
}
.primary-nav a:link, .primary-nav a:visited {
  color: inherit;
  text-decoration: none;
}
.primary-nav__shopBtn {
  color: var(--nitro-blue);
}

.margin {
  width: clamp(16rem, 90%, 1200px);
  margin: 6rem auto;
}

button {
  position: relative;
  margin: 2rem 0;
  width: 11.25rem;
  color: white;
  padding: 1.25rem;
  font-size: 1.25rem;
  background-color: var(--nitro-blue);
  border: none;
  cursor: pointer;
}
button::after {
  position: absolute;
  content: "";
  top: -0.35rem;
  left: -0.35rem;
  width: 100%;
  height: 100%;
  border: 2px solid black;
  transition: all 0.1s ease-out;
}
button:focus::after {
  top: 0;
  left: 0;
  transition: all 0.1s ease-out;
}

img, svg, picture {
  width: 100%;
  display: block;
}

html {
  box-sizing: border-box;
  font-size: 16px;
  text-rendering: optimizeLegibility;
  font-optical-sizing: auto;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Open Sans";
  font-weight: 400;
  margin: 0;
  padding: 0;
}

section {
  padding: 8rem 0;
}

h1,
h2 {
  font-weight: 300;
  font-size: 3.75rem;
  margin-bottom: 1rem;
}

h3,
.primary-nav__heading,
a .primary-nav__heading {
  font-weight: 300;
  font-size: 2.063rem;
}

p {
  font-size: 1.125rem;
  margin: 1rem 0;
}

a:link {
  color: inherit;
  text-decoration: none;
}

.card {
  position: relative;
  background-color: var(--card-background);
  border-radius: 10px;
  display: inline-flex;
  flex-direction: column;
  width: 330px;
  height: 440px;
  box-shadow: 0 5px 9px -1px rgba(0, 0, 0, 0.25);
  overflow: hidden;
}
.card__img {
  margin: auto;
  height: 17.5rem;
  width: auto;
  padding: 1rem;
}
.card__title {
  font-size: 1.75rem;
  margin: 0;
}
.card__price {
  color: var(--nitro-blue);
  font-size: 2rem;
  font-weight: bold;
}
.card__subtitle {
  color: var(--grey);
}
.card__content {
  width: 100%;
  display: inherit;
  flex-direction: row;
  padding: 1rem;
  background-color: var(--nitro-pink);
}
.card__content > :first-child {
  align-self: flex-end;
  align-items: flex-start;
  flex-grow: 1;
}
.card__content > :first-child p {
  margin: 0;
}
.card__price {
  display: flex;
}
.card::after {
  content: "";
}
.card:focus::after {
  position: absolute; /* The absolute position we mentioned earlier */
  top: 50%;
  left: 50%;
  border-radius: 50%;
  transform: scale(0);
  animation: ripple 600ms linear;
  background-color: rgba(255, 255, 255, 0.7);
}

@keyframes ripple {
  to {
    transform: scale(4);
    opacity: 0;
  }
}
.product {
  background: var(--light-grey);
}
.product__menu {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  row-gap: 4rem;
  column-gap: 4rem;
}
.product h2, .product__button {
  text-align: center;
}

.hero {
  display: flex;
  background-color: var(--nitro-pink);
}
.hero__content {
  display: flex;
  justify-content: space-between;
}
.hero > :first-child {
  background-color: var(--nitro-pink);
  display: inherit;
}
.hero__headline {
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-width: 25rem;
  column-gap: 10rem;
}
.hero__image {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  height: 100%;
  flex-grow: 1;
}
.hero__image__display {
  height: auto;
  min-width: 300px;
  max-width: 400px;
}
.hero__icon > :first-child {
  padding: 0;
  display: flex;
  justify-content: space-between;
  list-style: none;
}
.hero__icon > :first-child img {
  width: 50px;
}
.hero__can {
  aspect-ratio: 1/1;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-grow: 0;
  height: 100%;
  background-size: cover;
  animation: background-rotate 2s infinite;
  transition: 0.3s ease-in-out;
}
.hero__can__inner {
  height: auto;
  width: auto;
  text-align: center;
}

.discounts {
  text-align: center;
  padding: 1.25rem;
  width: 90%;
  font-size: 2.8125rem;
  color: white;
  background-color: var(--graphite);
}

@keyframes background-rotate {
  from {
    rotate: 0deg;
  }
  to {
    rotate: 360deg;
  }
}